<template lang="html" >
  <div id="write-page">
    <Head-view></Head-view>
    <Details  :showDetails='showDetails' :details='article'>
            </Details>
    <h2 class="write-title">
      {{article.title}}
    </h2>
    <Editor :editorContent='editorContent'></Editor>
    <div id="submit-article" @click='submitArticle'>
      <a href="#"></a>
    </div>
    <Dialog :dialog='dialog'></Dialog>
  </div>
</template>

<script>
/*
*@  :
*@    1.new,
*@    2.idid
*@    3.dataarcile ==> detaileditor
*/

import Head from '../components/head'
import Editor from '../components/editor'
import Details from '../components/article_details'
import Dialog from '../components/dialog'

const url = 'http://www.wwwung.cn/api/submitArticle'
const editUrl = 'http://www.wwwung.cn/api/edit?id='
export default {
  components: {
    'Head-view': Head,
    'Editor': Editor,
    Details,
    Dialog
  },
  data () {
    return {
      article: {
        title: '',
        time: '',
        userId: 1,
        type: 1,
        content: '',
        mold: 0,
        up: 0,
        textContent: null
      },
      dialog: {
        width: 600,
        height: 300,
        msg: '...',
        show: false
      },
      showDetails: true,
      editorContent: {
        htmlContent: '',
        textContent: ''
      }
    }
  },
  methods: {
    submitArticle () {
      if (!this.editorContent.htmlContent) {
        this.dialog = {
          width: 300,
          height: 100,
          msg: '',
          show: true
        }
        this.letDialogClear(this, 1000)
      } else {
        this.dialog = {
          width: 300,
          height: 100,
          msg: '...',
          show: true
        }
        this.article.time = (new Date()).getTime()
        this.article.content = this.editorContent.htmlContent
        this.article.textContent = this.editorContent.textContent
        // JSONoptionspost...
        if (this.$route.params.id !== 'new') {
          //  idnew artilceid
          this.article.id = this.$route.params.id
        }
        this.$http.post(url, JSON.stringify(this.article)).then((d) => {
          this.dialog.msg = ''
          this.letDialogClear(this, 1000, () => {
            this.$router.push({path: '/'})
          })
        }).catch((err) => {
          this.dialog.msg = ''
          this.letDialogClear(this, 1000)
          console.log(err)
        })
      }
    },
    // ...
    getDetails (detail) {
      Object.assign(this.article, detail)
    },
    // fun
    letDialogClear (_this, time, fun) {
      let timer = setTimeout(() => {
        _this.dialog.show = false
        clearTimeout(timer)
        if (typeof fun === 'function') {
          fun()
        }
      }, time)
    },
    getChangedBlog () {
      if (this.$route.params.id === 'new') {
        return false
      }
      this.$http.get(editUrl + this.$route.params.id).then((d) => {
        this.article.title = d.data.title
        this.article.content = d.data.content
        this.article.type = d.data.type
        this.article.mold = d.data.mold
        this.article.up = d.data.up
      })
    }
  },
  created () {
    this.getChangedBlog()
  }
}
</script>

<style lang="css" scoped>
#write-page {
  padding: 0 20px 10px;
  margin-top: 60px;
}
#submit-article {
  margin-top: 15px;
}
.write-title {
  line-height: 50px;
}
</style>



