<template lang="html">
  <div id="page-mask" v-show='isShow'>
    <div id="details-wrap">
      <div class="details-row">
        <div class="details-row-left">
          <span class="detail-name"></span>
        </div>
        <div class="details-row-right">
          <input type="text" class="detail-input" placeholder="" v-model='details.title'>
        </div>
      </div>
      <div class="details-row">
        <div class="details-row-left">
          <span class="detail-name"></span>
        </div>
        <div class="details-row-right">
          <span class="checkbox-wrap">
            <input type="radio" name="mold1" value=0 v-model='details.mold'>
            <label for="mold1"></label>
            <input type="radio" name="mold2" value=2 v-model='details.mold' >
            <label for="mold2"></label>
            <input type="radio" name="mold3" value=1 v-model='details.mold' >
            <label for="mold3"></label>
          </span>
        </div>
      </div>
      <div class="details-row">
        <div class="details-row-left">
          <span class="detail-name"></span>
        </div>
        <div class="details-row-right">
          <span class="checkbox-wrap">
            <input type="radio" name="type1" value=0 v-model='details.type'>
            <label for="type1"></label>
            <input type="radio" name="type2" value=2 v-model='details.type' >
            <label for="type2"></label>
            <input type="radio" name="type3" value=1 v-model='details.type' >
            <label for="type3"></label>
          </span>
        </div>
      </div>
      <div class="details-row">
        <div class="details-row-left">
          <span class="detail-name"></span>
        </div>
        <div class="details-row-right">
          <span class="checkbox-wrap">
            <input type="radio" name="up1" value=1 v-model='details.up'>
            <label for="up1"></label>
            <input type="radio" name="up2" value=0 v-model='details.up'>
            <label for="up2"></label>
          </span>
        </div>
      </div>
      <div class="details-row">
        <a href="javascript:;" class="detail-name" @click='submitClick'></a>
        <a href="javascript:;" class="detail-name" @click='closeWritePage'></a>
      </div>
    </div>
    <Dialog :dialog='dialog'></Dialog>
  </div>
</template>

<script>
/*
*@  :
*@    1.details(id)
*@  :
*@    2.vueprops,vuewarn
*@      @emit ===> 
*@      (...)
*/

import Dialog from './dialog'
export default {
  components: {
    Dialog
  },
  data () {
    return {
      dialog: {
        width: 300,
        height: 100,
        msg: '...',
        show: false
      },
      isShow: true
    }
  },
  props: {
    showDetails: {
      type: Boolean,
      default: false
    },
    details: {
      type: Object,
      default () {
        return {
          type: 1,
          up: 0,
          title: '',
          mold: 0
        }
      }
    }
  },
  methods: {
    submitClick () {
      //  ()
      if (!this.details.title) {
        this.dialog.msg = ''
        this.dialog.show = true
        const _this = this
        let timer = setTimeout(() => {
          _this.dialog.show = false
          clearTimeout(timer)
        }, 1000)
      } else {
        this.isShow = false
      }
    },
    closeWritePage () {
      this.$router.push({path: '/'})
    }
  }
}
</script>

<style lang="css">
#page-mask {
  top: 0px;
  bottom: 0px;
  position: absolute;
  left: 0;
  right: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}
#details-wrap {
  width: 600px;
  height: 400px;
  box-sizing: border-box;
  border: 2px solid black;
  border-radius: 12px;
  background: #eee;
  padding: 60px;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  position: absolute;
  font-size: 15px;
}
.detail-input {
  height: 28px;
  line-height: 28px;
  font-size: 15px;
  padding-left: 10px;
  border-radius: 3px;
}
.details-row {
  display: flex;
  justify-content: space-around;
  line-height: 28px;
}
.details-row-left {
  text-align: center;
}
.details-row-right {
  text-align: center;
}
.details-row-left {
  width: 160px;
}
.details-row-right {
  width: 320px;
}
</style>



