const io = socketio('http://www.wwwung.cn/api/')
Vue.use(VueSocketio, 'http://www.wwwung.cn/api/')

axios.defaults.withCredentials = true
Vue.use(VueAxios, axios)

Vue.config.productionTip = false

const url = 'http://www.wwwung.cn/api/isLogin'

