/*
*@  sessionsessionIDcookiecookiesessionID
*@  cookiesessionIDsessionsessionID
*@  store
*@  storeajaxcookiecookiesessionstore
*@  storecookie
*/
router.beforeEach((to, from, next) => {
  if (store.state.isLogin) {
    io.emit('login', {
      ioId: io.id,
      userId: store.state.user.id
    })
    if (to.meta.isBloger) {
      if (store.state.user.power === '0') {
        next()
      } else {
        next('/')
      }
    } else {
      next()
    }
  } else {
    axios.post(url).then((d) => {
      if (d.data === '') {
        io.emit('login', {
          ioId: io.id,
          userId: 0
        })
        if (to.meta.isBloger) {
          next('/login')
        } else {
          next()
        }
      } else {
        io.emit('login', {
          ioId: io.id,
          userId: d.data.id
        })
        store.dispatch('setUserInfo', d.data)
        store.dispatch('setLoginState', true)
        if (to.meta.isBloger) {
          if (d.data.power === '0') {
            next()
          } else {
            next('/')
          }
        } else {
          next()
        }
      }
    }).catch(err => {
      console.log(err)
      next()
    })
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})

